<template>
  <div class="success__head">
    <img src="@/assets/successRegistration/MiaoHui/icon_title.png" alt="">
    <h5>报名成功</h5>
    <p>打开<span>【{{ title }}】</span>小程序<br />进群学习</p>
    <div class="success__head__copy">
      <img :src="image" alt="">
      <h5>{{ title }}</h5>
      <button @click="goMini"><img src="@/assets/successRegistration/MiaoHui/icon_open.gif" alt=""></button>
    </div>
  </div>
</template>

<script>
import { getSearch } from '@/utils'
import { appid } from '@/config'
import iconOfficial from '@/assets/successRegistration/MiaoHui/icon_official.png'
export default {
  data () {
    return {
      title: '秒会财商课',
      image: iconOfficial
    }
  },
  async created () {

  },
  methods: {
    async goMini () {
      this.$log.click({
        module_ori: '基于页面',
        module_part: '106_toufang_yjj_0394'
      })
      const query = getSearch()
      const phone = localStorage.getItem('buyPhone') || ''
      const orderNum = localStorage.getItem('orderNum')
      const { url } = await this.$apis.userOperator.getMiniHref({
        path: 'callback/pages/loading/loading',
        appid: appid,
        query: { ...query, phone, orderNum }
      })
      window.location.href = url
    }
  }
}
</script>

