<template>
  <div class="SecondLiveResult">
    <img
      src="@/assets/callback/title.jpg"
      class="header"
      alt="" />
    <div class="qrcode">
      <img
        class="code"
        :src="url"
        alt="" />
      <div class="tips">*老师会在24小时内通过好友申请，并告知详细课程安排</div>
    </div>
    <img
      src="@/assets/callback/3.png"
      class="footer"
      alt="" />
  </div>
</template>
<script>
import { getSearch } from '@/utils'
export default {
  data () {
    return {
      url: ''
    }
  },
  created () {
    this.getCode()
    const query = getSearch()
    const uid = localStorage.getItem('uid_recall')
    this.$log.update({
      alternate: JSON.stringify({ message_touch: 1, message_id: query.message_id, activity_id: query.activity_id, uid_recall: uid, uv4recall: '1', screen_loc: 2 }),
      current_page: location.href
    })
    this.$log.show({
      module_ori: '短信→H5分班入口-浏览器H5老师二维码页',
      module_part: '107_h5_xbzb_formal__hyj_0049'
    })
  },
  methods: {
    async getCode () {
      const form = {
        phone: localStorage.getItem('callback-phone'),
        code: localStorage.getItem('callback-code')
      }
      const query = getSearch()
      const data = await this.$apis.userOperator.getQrCode({
        ...form,
        message_id: query.message_id,
        activity_id: query.activity_id
      }).catch(err => {
        console.log(err)
        this.$dialog.close()
        this.$router.push({
          name: 'SecondLive'
        })
      })
      console.log(data)
      this.url = data.qr_url
    }
  }
}
</script>
